
const updateForecast = (data) => {
    return {
        type: 'UPDATE_FORECAST',
        payload: data
    }
}

const searchByCity = (city) => {
    return {
        type: 'SEARCH_BY_CITY',
        payload: city
    }
}

const onWaiting = () => {
    return {
        type: 'WAIT'
    }
}

export {
    updateForecast, searchByCity, onWaiting
}