import React, { Component } from 'react';
import { connect } from 'react-redux';
import { searchByCity, updateForecast, onWaiting } from '../../actions';
import withService from '../hoc';
import WeatherCard from '../weather-card';
import ErrorBoundry from '../error-boundry';
import Waiting from '../waiting';
import {
    Container, Textarea, SearchButton, ClearButton, Li
} from './styles';

class Main extends Component {

    state = {
        city: 'London'
    }

    compareData = (data) => {
        const { state } = this.props;
        const list = data.list;
        let arr = [], arrInner = [], day = 0, idx = 0;
        for (let i = 0; i >= 0; i++) {
            if (!list[i]) {
                break;
            }
            let data = new Date(list[i].dt * 1000);
            if (day !== data.getDate()) {
                day = data.getDate();
                if (arrInner.length) {
                    arr.push(arrInner)
                }
                arrInner = [];
            }
            const obj = {
                id: idx,
                date: list[i].dt,
                day: data.getDate(),
                dayWeek: state.weekDays[data.getDay()],
                time: data.getHours() < 10 ? `0${data.getHours()}:00` : `${data.getHours()}:00`,
                windy: list[i].wind.speed,
                cloudly: list[i].clouds.all,
                humidity: list[i].main.humidity,
                temp: list[i].main.temp,
                weather: list[i].weather[0].main
            }
            arrInner.push(obj);
            idx++;
        }
        return arr;
    }

    componentDidMount() {
        const { service, updateForecast, state } = this.props;
        service.getWeather(state.searchByCity)
            .then((data) => {
                if (data.list) {
                    updateForecast(this.compareData(data))
                } else {
                    updateForecast(data)
                }
            })
    }

    onChange = (e) => {
        e.preventDefault();
        const { onWaiting } = this.props;

        this.setState({
            ...this.state,
            city: e.target.value
        })
        onWaiting();
    }

    onClear = (e) => {
        e.preventDefault();
        this.setState({
            ...this.state,
            city: ''
        })
        this.onChange(e);
    }

    onSearch = (e) => {
        e.preventDefault();
        const { state, service, updateForecast, searchByCity } = this.props;
        if (this.state.city.trim()) {
            searchByCity(this.state.city.trim())
            service.getWeather(this.state.city.trim())
                .then((data) => {
                    if (data.list) {
                        updateForecast(this.compareData(data));
                    } else {
                        updateForecast(data);
                    }
                })
        } else {
            this.setState({
                ...this.state,
                city: state.defaultCity
            })
            service.getWeather(this.state.defaultCity)
                .then((data) => {
                    if (data.list) {
                        updateForecast(this.compareData(data))
                    }
                })
        }
    }

    render() {
        let delay = -4;
        const { state } = this.props;

        return (
            <Container>
                <Textarea onSubmit={this.onSearch}>
                    <ClearButton type='reset' onClick={this.onClear}></ClearButton>
                    <input placeholder="Write Your City" onChange={this.onChange} value={this.state.city} />
                    <SearchButton onClick={this.onSearch}></SearchButton>
                </Textarea>
                {state.errorBoundry ? <ErrorBoundry caption={state.errorType} /> :
                    state.isWaiting ? <Waiting /> :
                        <ul>
                            {!state.isLoading && state.forecast.map((item) => {
                                delay = delay === 0 ? delay + 4 : delay === 4 ? 4 : delay + 4;
                                return <Li style={{ animationDelay: `${delay}s` }} key={item[0].id}><WeatherCard data={item} /></Li>
                            })}
                        </ul>
                }
            </Container>
        )
    }

}

const mapStateToProps = (state) => {
    return { state };
}

const mapDispatchToProps = { searchByCity, updateForecast, onWaiting }

export default withService()(connect(mapStateToProps, mapDispatchToProps)(Main));