import React, { Component } from 'react';
import moment from 'moment-timezone';
import cityTimezones from 'city-timezones';
import { Container, Logo, CityNow, Wrapper } from './styles';
import { connect } from 'react-redux';
import Spinner from '../spinner';

class Header extends Component {

    state = {
        city: 'London',
        mm: moment().tz("Europe/London").format('HH:mm')
    }

    onUpdateTime = () => {
        const { searchCity } = this.props.state;

        const stateCity = searchCity.replace(/(^|\s)\S/g, (s) => s.toUpperCase());
        const city = cityTimezones.lookupViaCity(stateCity);
        const findItem = city.filter((item) => item.timezone.includes(stateCity.replace(' ', '_')));

        if (findItem[0]) {
            this.setState({
                city: stateCity,
                mm: moment().tz(findItem[0].timezone).format('HH:mm')
            })
        }

    }

    onGetTime = (cityName) => {
        const stateCity = cityName.replace(/(^|\s)\S/g, (s) => s.toUpperCase());
        const city = cityTimezones.lookupViaCity(stateCity);
        const findItem = city.filter((item) => item.timezone.includes(stateCity.replace(' ', '_')));

        if (findItem[0]) {
            return moment().tz(findItem[0].timezone).format('HH:mm')
        }
        return 'Ops...';
    }

    render() {
        const { isLoading, searchCity } = this.props.state;
        const timeCity = this.onGetTime(searchCity);

        return (
            <Wrapper>
                <Container>
                    {isLoading ? <Spinner>Wea</Spinner> : <Logo>Wea</Logo>}
                    {/* <span>{timeCity}</span> */}
                    <CityNow>{timeCity}</CityNow>
                    {/* <CityNow>{searchCity}</CityNow> */}
                </Container>
            </Wrapper>
        )
    }

    componentDidMount() {
        this.int = setInterval(this.onUpdateTime, 1000);
    }

    componentDidUpdate() {
        const { searchCity } = this.props.state;
        if (searchCity.toLowerCase() !== this.state.city.toLowerCase()) {
            this.onGetTime(searchCity);
        }
    }

}

const mapStateToProps = (state) => {
    return { state }
}

export default connect(mapStateToProps)(Header);