import styled, { keyframes } from 'styled-components';


const appearSide = keyframes`
    0% {
        opacity: 0;
        transform: scale(1.2);
    }
    100% {
        opacity: 1;
        transform: scale(1);
    }
`

const appearTop = keyframes`
    0% {
        opacity: 0;
        transform: translateY(10px);
    }
    100% {
        opacity: 1;
        transform: translateY(0px);
    }
`

const appearLeft = keyframes`
    0% {
        opacity: 0;
        transform: translate(-10px);
    }
    100% {
        opacity: 1;
        transform: translate(0px);
    }
`

const appearScale = keyframes`
    0% {
        opacity: 0;
        transform: scale(.2);
    }
    100% {
        opacity: 1;
        transform: scale(1);
    }
`

const appearSize = keyframes`
    0% {
        opacity: 0;
    }
    100% {
        opacity: 1;
    }
`

const Wrapper = styled.div`
    position: relative;
    max-width: 400px;
    z-index: 2;
`

const Container = styled.div`
    margin-top: 25px;
    width: 100%;
    max-width: 400px;
    padding: 15px;
    background-color: #F7F7F7;
    border-radius: 5px;
    border: ${props => props.border};
    box-shadow: 0px 0px 20px 2px rgba(65, 122, 255, 0.25);
    display: flex;
    justify-content: space-between;
    z-index: 5;
    position: relative;
    transition: 1s;
    @media (min-width: 771px) {
        box-shadow: 0px 0px 20px 2px rgba(65, 122, 255, 0.25);
        border-radius: 5px 5px 0px 0px;
        border-bottom: none;
    }
`

const ChartWrapper = styled.div`
    padding: 15px;
    border-radius: 5px;
    z-index: 5;
    position: relative;
    border: ${props => props.border};
    max-width: 400px;
    margin-top: 25px;
    box-shadow: 0px 0px 20px 2px rgba(65, 122, 255, 0.25);
    background-color: #F7F7F7;
    & canvas {
        animation: ${appearSize} 1s forwards ease-out;
    }
`

const Flex = styled.div`
    display: flex;
    align-items: ${props => props.center ? 'center' : 'stretch'};
    justify-content: space-between;
    flex-direction: column;
`

const Date = styled.div`
    display: flex;
    flex-direction: column;
    opacity: 0;
    animation: ${appearLeft} 1s .5s forwards ease-out;
    text-align: left;
    & div {
        font-family: 'Nunito-bold';
        font-size: 20px;
        line-height: 27px;
        color: #000000;
    }
    & span {
        font-family: 'Nunito-regular';
        font-size: 12px;
        line-height: 16px;
        color: rgba(0, 0, 0, 0.5);
    }
`

const Icon = styled.div`
    pointer-events: none;
    width: 100px;
    height: 100px;
    & svg, img {
        object-fit: cover;
        width: inherit;
        height: inherit;
    }
    opacity: 0;
    animation: ${appearSide} .9s 3s forwards ease-out;
    transition: 1s;
`

const InnerIcon = styled.div`
    pointer-events: none;
    width: 30px;
    height: 30px;
    & img {
        object-fit: cover;
        width: inherit;
        height: inherit;
    }
`

const Grid = styled.div`
    text-align: left;
    margin-top: 40px;
    display: grid;
    grid-template-columns: 40px 70px 70px;
    align-items: center;
    opacity: 0;
    animation: ${appearTop} 1s 1.2s forwards ease-out;
    & span {
        font-family: 'Nunito-regular';
        font-size: 15px;
        line-height: 20px;
        color: rgba(0, 0, 0, 0.5);
    }
    & span + div {
        font-family: 'Nunito-bold';
        font-size: 15px;
        line-height: 20px;
        color: rgba(0, 0, 0, 0.6);
    }
`

const Temp = styled.span`
    margin-top: 20px;
    font-family: 'Nunito-regular';
    font-size: 30px;
    line-height: 41px;
    color: #000000;
    opacity: 0;
    animation: ${appearScale} .9s 2s forwards ease-out;
`

export {
    Container, Flex, Date, Icon, Wrapper, Grid, InnerIcon, Temp, ChartWrapper
};