import styled, { keyframes } from 'styled-components';

const appear = keyframes`
    0% {
        transform: scale(.9);
        opacity: 0;
    }
    100% {
        transform: scale(1);
        opacity: 1;
    }
`

const apperHalf = keyframes`
    0% {
        transform: scale(.7);
        opacity: 0;
    }
    100% {
        transform: scale(1);
        opacity: .6;
    }
`

const Container = styled.div`
    animation: ${appear} 1s forwards ease-out;
    margin-top: 25px;
    width: 100%;
    max-width: 400px;
    padding: 15px;
    background-color: #F7F7F7;
    border-radius: 5px;
    border: 2px solid #DC4545;
    box-shadow: 0px 0px 20px 2px rgba(220, 69, 69, 0.25);
    display: flex;
    justify-content: space-between;
    & img {
        width: inherit;
    }
`

const Text = styled.div`
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    & h1 {
        opacity: 0;
        animation: ${appear} 1s .3s forwards ease-out;
        font-family: 'Nunito-Bold';
        font-size: 1em;
        color: #3E3E3E;
    }
    & span {
        display: block;
        margin-top: 20px;
        font-family: 'Nunito-regular';
        font-size: .7em;
        opacity: 0;
        color: #3E3E3E;
        animation: ${apperHalf} .7s 4s forwards ease-out;
    }
`

const Icon = styled.div`
    text-align: right;
    width: 70%;
    opacity: 0;
    animation: ${apperHalf} .7s 2s forwards ease-out;
`

export {
    Container, Text, Icon
};