import styled, { keyframes } from 'styled-components';


const showOn = keyframes`
    0% {
        opacity: 0;
    }
    100% {
        opacity: 1;
    }
`

const Container = styled.div`
    border: ${props => props.border};
    background-color: #fff;
    box-shadow: 0px 1px 23px rgba(65, 122, 255, 0.25);
    width: 95%;
    max-width: 400px;
    margin: 0 auto;
    border-top: none;
    border-radius: 0px 0px 5px 5px;
    transform-origin: top;
    position: relative;
    margin-top: ${props => props.margin || '0px'};
    opacity: ${props => props.margin ? '0' : '1'};
    /* animation: ${showOn} 1s 1s forwards ease-out; */
    transition: ${props => props.margin ? '2s' : '.8s'};
    transform: ${props => props.margin ? 'scaleY(0)' : 'scaleY(1)'};
    @media (min-width: 771px) {
        margin-top: 0px;
        width: 100%;
        opacity: 1;
        transform: scaleY(1);
    }
`

const Wrapper = styled.div`
    width: 100%;
    padding: 15px;
    display: flex;
    justify-content: space-between;
`

const Item = styled.div`
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
`

const Content = styled.div`
    display: flex;
    justify-content: space-between;
`

const Grid = styled.div`
    display: grid;
    grid-template-columns: 70px 65px;
    line-height: 24px;
    & span {
        font-family: 'Nunito-regular';
        font-size: 14px;
        color: rgba(0, 0, 0, 0.5);
    }
    & div {
        font-family: 'Nunito-bold';
        font-size: 13px;
        color: rgba(0, 0, 0, 0.6);
    }
`

const Temperature = styled.div`
    margin-top: auto;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    & span {
        font-family: 'Nunito-regular';
        margin-top: 10px;
        display: flex;
        font-size: 25px;
        line-height: 34px;
        color: #000000;
    }
`

const Icon = styled.div`
    width: 40px;
    height: 40px;
    opacity: .5;
`

const Hours = styled.div`
    display: flex;
    flex-direction: column;
    padding: 10px 5px;
    height: 170px;
    border-left: 1px solid rgba(148,148,148, .1);
    & div {
        overflow-y: auto;
        display: inline-flex;
        flex-direction: column;
    }
    & span {
        font-family: 'Nunito-regular';
        opacity: .6;
        margin: 2px 0px;
        padding: 7px 10px;
        font-size: 15px;
        transition: .3s;
        color: #000;
        border-radius: 5px;
    }
    & span:hover {
        opacity: 1;
    }
    & span.show {
        transition: .3s;
        color: #fff;
        opacity: 1;
        background-color: rgba(65,122,255);
    }
`
const IconsInfo = styled.div`
    pointer-events: none;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
`

const HumanIcon = styled.div`
    width: 50px;
    height: 80px;
`

const Date = styled.span`
    opacity: .7;
    display: block;
    margin: 0px 0px 10px 0px;
    font-family: 'Nunito-regular';
    font-size: 12px;
    line-height: 16px;
    color: rgba(0, 0, 0, 0.5);
`

export {
    Container, Content, Date, Grid, Temperature, Icon, Item, Hours, Wrapper, HumanIcon, IconsInfo
};