export default class Services {

    errors = {
        404: 'Unfortunately, the city was not found.',
        401: 'service not working.'
    }

    days = 'https://api.openweathermap.org/data/2.5/forecast?q=london&appid=78edeadf8a6a325f53559aab91892dbb';
    now = 'https://api.openweathermap.org/data/2.5/weather?q=london&appid=9c84cd2c676365232a2392dca3a2b7c5';

    getWeather(city = 'london') {
        return fetch(`https://api.openweathermap.org/data/2.5/forecast?q=${city}&appid=78edeadf8a6a325f53559aab91892dbb`)
            .then(response => response.json())
            .then(json => {
                if (Number(json.cod) !== 200) {
                    return this.errors[json.cod] ? this.errors[404] : "Something went wrong!"
                }
                return json
            })
            .catch(json => "")
    }

}