import React from 'react';
import {WeaConsumer} from '../context';

const withService = () => (Wrapper) => {
    return (props) => {
        return (
            <WeaConsumer>
                {(service) => {
                    return (
                        <Wrapper {...props} service={service} />
                    )
                }}
            </WeaConsumer>
        )
    }
}

export default withService;