import styled, { keyframes } from 'styled-components';

const typing = keyframes`
    0% {
        content: '';
        opacity: 0;
    }
    20% {
        content: '.';
        opacity: 1;
    }
    40% {
        content: '..';
    }
    60% {
        content: '...'
    }
    80% {
        opacity: 0;
        content: '...'
    }
    90% {
        content: '...'
    }
    100% {
        opacity: 0;
        content: '';
    }

`

const Text = styled.div`
    margin-top: 50px;
    font-family: 'Nunito-Bold';
    font-size: 1.2em;
    opacity: .2;
    transition: 3s;
    &:after {
        content: '';
        color: #000;
        animation: ${typing} 5s infinite linear;
        font-family: 'Nunito-Bold';
        z-index: 20;
    }
    &:active {
        transition: .2s;
        opacity: 1;
    }
`

export {
    Text
};