const weatherIcons = {
    cloud: ['img/hailstorm.svg', 'img/autumn.svg'],
    rain: ['img/raining.svg', 'img/cold.svg'],
    snow: ['img/snow.svg', 'img/autumn.svg'],
    sun: ['img/sun.svg', 'img/sunny.svg']
}

const getIcon = (weather) => {
    switch (weather.toLowerCase()) {
        case 'clouds':
            return weatherIcons.cloud;
        case 'sun':
            return weatherIcons.sun;
        case 'snow':
            return weatherIcons.snow;
        case 'rain':
            return weatherIcons.rain;

        default:
            return weatherIcons.sun;
    }
}

export default getIcon;