import React, { Component } from 'react';
import { Line } from 'react-chartjs-2';

class Chart extends Component {

    constructor(props) {
        super(props)
        this.state = {
            data: {
                labels: this.props.data.map((item) => item.time.split('')[0] + item.time.split('')[1] + 'h'),
                datasets: [
                    {
                        label: 'Temperature',
                        data: this.props.data.map((item) => Math.floor(item.temp - 273.15)),
                        borderWidth: '1',
                        borderColor: '#3564D0',
                        backgroundColor: 'rgba(65,122,255, .3)'
                    }
                ]
            }
        }
    }

    render() {

        return (
            <div>
                <Line
                    data={this.state.data}
                    width={100}
                    height={70}
                />
            </div >
        )
    }
}

export default Chart;