import React from 'react';
import {
    Container, Text, Icon
} from './styles';

const ErrorBoundry = ({ caption }) => {
    return (
        <Container>
            <Text>
                {caption ?
                    <>
                        <h1>{caption}</h1>
                        <span>Please, try another city.</span>
                    </>
                    :
                    <>
                        <h1>Ops... Something went wrong!</h1>
                        <span>Please, check your network, or try later.</span>
                    </>
                }
            </Text>
            <Icon>
                <img src="img/support.svg" alt="error" />
            </Icon>
        </Container>
    )
}

export default ErrorBoundry;