const breathe = keyframes`
    0% {
        transform: scale(1);
    }
    50% {
        transform: scale(1.06);
    }
    100% {
        transform: scale(1);
    }
`

const loading = keyframes`
    0% {
        opacity: 0;
        content: '';
    }
    25% {
        opacity: 1;
        content: '.';
    }
    50% {
        content: '..';
    }
    75% {
        opacity: 1;
        content: '...';
    }
    86% {
        opacity: 0;
    }
    100% {
        opacity: 0;
        content: '';
    }

`

const Spin = styled.div`
    font-size: 25px;
    font-family: 'Nunito-Bold';
    font-family: 'Nunito-Black';
    color: #fff;
    position: relative;
    animation: ${breathe} 3s linear infinite;
    &:after {
        content: '';
        position: absolute;
        animation: ${loading} 4s linear infinite;
    }
`

export {
    Spin
}