import styled, { keyframes } from 'styled-components';

const success = keyframes`
    0% {
        left: 0;
        width: 0%;
    }
    50% {
        left: 0;
        width: 100%;
    }
    100% {
        left: 100%;
        right: 0;
        width: 0%;
    }
`

const Wrapper = styled.div`
    width: 100%;
    min-height: 60px;
    background-color: #417AFF;
    display: flex;
    align-items: center;
    justify-content: space-around;
    position: relative;
`

const Container = styled.header`
    display: flex;
    align-items: center;
    justify-content: space-between;
    transition: 1s;
    width: 100%;
    max-width: 90%;
    margin: 0 auto;
    & span {
        display: block;
        margin: 0px 10px;
        color: #fff;
        font-family: 'Nunito-Bold';
        font-feature-settings: "thum";
        font-variant-numeric: tabular-nums;
        font-size: 1em;
        letter-spacing: 0.05em;
    }
`
const CityNow = styled.div`
    position: absolute;
    top: 100%;
    left: 50%;
    transform: translate(-50%, 0%);
    font-size: 1.5em;
    color: #666666;
    font-family: 'Nunito-Bold';
    text-transform: capitalize;
    opacity: .2;
    z-index: 2;
`

const Logo = styled.div`
    font-size: 25px;
    font-family: 'Nunito-Black';
    color: #fff;
    position: relative;
    transition: 1s;
    &:after {
        content: '';
        position: absolute;
        bottom: -2px;
        left: 0;
        height: 5px;
        width: 0%;
        background-color: #24E138;
        border-radius: 2px;
        animation: ${success} 2s forwards ease-out;
    }
`

const Search = styled.input`
    padding: 7px 15px;
    border: none;
    border-radius: 4px;
    color: #353535;
    font-size: 17px;
    font-family: 'Nunito-Bold';
`

export {
    Container, Search, Logo, Wrapper, CityNow
}