import React, { Component, createRef } from 'react';
import moment from 'moment';
import getIcon from '../weather-icons';
import { Container, Wrapper, Content, Grid, Temperature, Icon, Item, Hours, HumanIcon, IconsInfo, Date } from './styles';

class WeatherHourly extends Component {
    constructor(props) {
        super(props);
        this.isClosed = createRef();
        this.state = {
            height: 170,
            idx: 0,
            list: this.props.data
        }
    }

    componentDidMount() {
        this.setState(() => {
            return {
                height: this.isClosed.current.offsetHeight
            }
        })
    }

    getTemp = (t) => {
        return Math.floor(t - 273.15)
    }

    changeTime = (id) => {
        this.setState(() => {
            return {
                ...this.state,
                idx: id
            }
        })
    }

    render() {
        const d = new window.Date();
        const { isOpened } = this.props;
        const st = this.state;
        console.log(st.list[st.idx]);
        const { day, windy, humidity, cloudly, temp, weather, dayWeek } = st.list[st.idx];

        return (
            <Container ref={this.isClosed} margin={isOpened && `-${this.state.height}px`} isOpened={isOpened} border={day === d.getDate() ? '3px solid #417AFF' : 'none'} >
                <Content>
                    <Wrapper>
                        <Item>
                            <Date>{day} {moment().format('MMM')}. {dayWeek}</Date>
                            <Grid>
                                <span>windy</span>
                                <div>{Math.floor(windy)} km/h</div>
                                <span>cloudly</span>
                                <div>{cloudly}%</div>
                                <span>humidity</span>
                                <div>{humidity}%</div>
                            </Grid>
                            <Temperature>
                                <span>{this.getTemp(temp)}° C</span>
                            </Temperature>
                        </Item>
                        <IconsInfo>
                            <Icon>
                                <img src={getIcon(weather)[0]} alt="weather" />
                            </Icon>
                            <HumanIcon>
                                <img src={getIcon(weather)[1]} alt="human" />
                            </HumanIcon>
                        </IconsInfo>
                    </Wrapper>
                    <Hours>
                        <div>
                            {st.list.map((item, indx) => {
                                return <span onClick={() => this.changeTime(indx)} key={item.id} className={indx === st.idx ? "show" : ''}>{item.time}</span>
                            })}
                        </div>
                    </Hours>
                </Content>
            </Container>
        )
    }
}

export default WeatherHourly;