import styled, { keyframes } from 'styled-components';


const rotate = keyframes`
    0% {
        transform: rotate(0deg);
    }
    100% {
        transform: rotate(180deg);
    }
`

const pulse = keyframes`
    0% {
        transform: scale(1);
    }
    50% {
        transform: scale(.9);
    }
    100% {
        transform: scale(1);
    }
`

const showCard = keyframes`
    0% {
        opacity: 0;
        transform: scale(.9);
    }
    100% {
        opacity: 1;
        transform: scale(1);
    }
`

const Textarea = styled.form`
    font-size: 30px;
    padding: 5px 0px;
    position: relative;
    min-height: 80px;
    background-color: transparent;
    display: flex;
    align-items: center;
    justify-content: space-between;
    & input {
        width: 100%;
        border: none;
        background-color: transparent;
        font-size: 27px;
        color: #474747;
        text-align: center;
        font-family: 'Nunito-Bold';
        text-transform: capitalize;
    }
    & input::placeholder{
        opacity: .5;
        font-size: 20px;
    }
    &:hover button {
        opacity: .3;
        pointer-events: auto;
        z-index: 2;
    }
    &:hover button:last-child {
        animation: ${pulse} 2.5s infinite linear;
    }
    & button:first-child:hover {
        animation: ${rotate} .4s ease-out;
    }
    & button {
        transition: 2s;
        border: none;
        border-radius: 5px;
        padding: 20px;
        background-color: transparent;
        opacity: 0;
        width: 30px;
        height: 30px;
        background-repeat: no-repeat;
        background-position: center center;
        z-index: -5;
        @media (min-width: 771px) {
            opacity: .3;
            pointer-events: auto;
            z-index: 2;
        }
    }
    & button:first-child:active {
        transition: .1s;
        background-color: #E6E6E6;
    }
    & button:last-child:active {
        transition: .1s;
        background-color: rgba(35,184,57, .8);
    }
`

const ClearButton = styled.button`
    background-image: url(/weather/img/close.svg);
    background-size: 20px 20px;
`

const SearchButton = styled.button`
    background-image: url(/weather/img/loupe.svg);
    background-size: 25px 25px;
`

const Container = styled.div`
    padding: 20px 0px;
    max-width: 90%;
    margin: 0 auto;
    & ul {
        
        @media (min-width: 771px) {
            display: grid;
            grid-template-columns: 1fr 1fr;
        }
        @media (min-width: 1300px) {
            display: grid;
            grid-template-columns: 1fr 1fr 1fr;
        }
        /* justify-content: space-between;
        flex-wrap: wrap; */
    }
`


const Li = styled.li`
    opacity: 0;
    animation: ${showCard} .6s forwards ease-out;
    @media (min-width: 771px) {
        margin: 10px 20px;
    }
`

export {
    Container, Textarea, SearchButton, ClearButton, Li
}