import React, { useState } from 'react';
import moment from 'moment';
import Chart from '../chart';
import WeatherHourly from '../weather-hourly';
import getIcon from '../weather-icons';
import {
    Wrapper, Container, Flex, Date, Icon, Grid, InnerIcon, Temp, ChartWrapper
} from './styles';


const WeatherCard = ({ data }) => {
    const d = new window.Date();
    const [isOpen, setIsOpen] = useState(true);

    const onOpen = () => {
        setIsOpen(() => !isOpen)
    }

    const { day, windy, humidity, cloudly, dayWeek, temp, weather } = data[0];

    return (
        <Wrapper>
            <div onClick={onOpen} >
                {!isOpen ? <ChartWrapper border={day === d.getDate() ? '3px solid #417AFF' : 'none'}><Chart data={data} /></ChartWrapper> :
                    <Container border={day === d.getDate() ? '3px solid #417AFF' : 'none'}>
                        <Flex>
                            <Date>
                                <div>{day === d.getDate() ? 'Today' : day === d.getDate() + 1 ? 'Tomorrow' : ''}</div>
                                <span>{day} {moment().format('MMM')}. {dayWeek}</span>
                            </Date>
                            <Grid>
                                <InnerIcon>
                                    <img src="img/windy.svg" alt="windy" />
                                </InnerIcon>
                                <span>windy</span>
                                <div>{Math.floor(windy)} km/h</div>
                                <InnerIcon>
                                    <img src="img/fog.svg" alt="fog" />
                                </InnerIcon>
                                <span>cloudly</span>
                                <div>{cloudly}%</div>
                                <InnerIcon>
                                    <img src="img/humidity.svg" alt="humidity" />
                                </InnerIcon>
                                <span>humidity</span>
                                <div>{humidity}%</div>
                            </Grid>
                        </Flex>
                        <Flex center>
                            <Icon>
                                <img src={getIcon(weather)[0]} alt="weather" />
                            </Icon>
                            <Temp>{Math.floor(temp - 273.15)}° C</Temp>
                        </Flex>
                    </Container>
                }
            </div>
            <WeatherHourly isOpened={isOpen} data={data} />
        </Wrapper>
    )
}

export default WeatherCard;