
const initialState = {
    isLoading: true,
    errorBoundry: false,
    errorType: '',
    isWaiting: false,
    weekDays: ['Monday', 'Tuesday', 'Wednesday', 'Thursday', 'Friday', 'Saturday', 'Sunday'],
    defaultCity: 'London',
    searchCity: 'London',
    forecast: []
}

const reducer = (state = initialState, action) => {

    switch (action.type) {
        case 'UPDATE_FORECAST':
            if (typeof action.payload === 'string') {
                return {
                    ...state,
                    isLoading: false,
                    isWaiting: false,
                    errorBoundry: true,
                    errorType: action.payload
                }
            }
            return {
                ...state,
                errorBoundry: false,
                isWaiting: false,
                isLoading: false,
                forecast: action.payload
            }

        case 'SEARCH_BY_CITY':
            return {
                ...state,
                isLoading: true,
                isWaiting: false,
                searchCity: action.payload
            }

        case 'WAIT':
            return {
                ...state,
                isWaiting: true,
                isLoading: false
            }

        default:
            return state;
    }

}

export default reducer;